/**
 * 加载单一插件
 * @param map
 * @param pluginName
 * @returns {Promise<any>}
 */
const loadPlugin = (map, pluginName) => {
  return new Promise((resolve) => {
    if (window.AMap[pluginName]) {
      resolve()
    } else {
      map.plugin(`AMap.${pluginName}`, () => {
        resolve()
      })
    }
  })
}

/**
 * 加载多个插件
 * @param map
 * @param pluginNames
 * @returns {Promise<void>}
 */
const loadPlugins = async (map, pluginNames) => {
  for (let name of pluginNames) {
    await loadPlugin(map, name)
  }
  return Promise.resolve()
}

/**
 * 加载UI plugins
 * @param pluginNames
 * @returns {Promise<any>}
 */
const loadUIPlugins = (pluginNames) => {
  return new Promise((resolve) => {
    window.AMapUI.loadUI(pluginNames, (...plugins) => {
      resolve(...plugins)
    })
  })
}




export {
  loadPlugin,
  loadPlugins,
  loadUIPlugins
}
