import ComponentMixins from '../../mixins/ComponentMixins'

/**
 * 插件参数
 * @type {{}}
 */
const parameter = {
  position: Array,
  anchor: String,
  offset: Object,
  icon: String | Object,
  content: String | Object,
  topWhenClick: Boolean,
  bubble: Boolean,
  draggable: Boolean,
  raiseOnDrag: Boolean,
  cursor: String,
  visible: {
    type: Boolean,
    default: true
  },
  zIndex: Number,
  angle: Number,
  autoRotation: Boolean,
  animation: String,
  shadow: Object,
  title: String,
  clickable: Boolean,
  shape: Object,
  extData: String | Number | Boolean | Object,
  label: Object
}

export default {
  name: 'amap-marker',
  mixins: [
    ComponentMixins
  ],
  props: Object.assign({}, parameter),
  data () {
    const _this = this
    return {
      /**
       * 属性监控事件
       */
      watchHandlers: {
        // 状态显示
        visible (_new, old) {
          if (_new !== old) {
            _new ? _this.$amapComponent.show() : _this.$amapComponent.hide()
          }
        }
      }
    }
  },
  created () {
    this.$componentParameter = parameter
  },
  methods: {
    /**
     * 创建组件
     */
    createComponent () {
      console.log('============')
      this.$amapComponent = new window.AMap.Marker(this.$componentCreateParameter)
      this.$amap.add(this.$amapComponent)
    }
  }
}
