import EventConstant from '../../src/constant/EventConstant'
import upperCamelCase from 'uppercamelcase'


/**
 * 组件混入
 */
export default {
  mounted() {
    this.$amap = this.$amap || this.$parent.$amap
    if (this.$amap) {
      this.init()
    } else {
      this.$on(EventConstant.AMAP_CREATE_EVENT, map => {
        this.$amap = map
        this.init()
      })
    }
  },
  methods: {
    init () {
      this.initParameter()
      const result = this.createComponent()
      // 添加属性监控
      if (result instanceof Promise) {
        result.then(() => {
          this.addParameterWatcher()
        })
      } else {
        this.addParameterWatcher()
      }
    },
    createComponent: {},
    /**
     * 初始化插件参数
     */
    initParameter () {
      const pluginParameter = this.getCustomCreateParameter()
      Object.keys(this.$componentParameter).forEach(key => {
        if (this[key] !== undefined && this[key] !== null) {
          pluginParameter[key] = this[key]
        }
      })
      this.$componentCreateParameter = pluginParameter
    },
    getCustomCreateParameter () {
      return {}
    },
    /**
     * 添加属性监控
     */
    addParameterWatcher () {
      Object.keys(this.$componentParameter).forEach(key => {
        const handler = this.getWatcherHandler(key)
        if (handler) {
          this.$watch(key, (_new, old) => {
            // 判断 使用amap方法 还是watchHandlers方法
            if (this.watchHandlers && this.watchHandlers[key]) {
              handler(_new, old)
            } else {
              handler.call(this.$amapComponent, _new)
            }
          })
        }
      })
    },
    /**
     * 获取监控执行器
     */
    getWatcherHandler (propName) {
      // 获取自定义方法
      if (this.watchHandlers && this.watchHandlers[propName]) return this.watchHandlers[propName]
      return this.$amapComponent[`set${upperCamelCase(propName)}`] || this.$amapComponent.setOptions
    }
  },
  /* eslint-disable */
  render (h) {
    return (
      <slot/>
    )
  }
}
