import { loadUIPlugins } from '../../../src/utils/PluginLoader'

import ComponentMixins from '../../mixins/ComponentMixins'

const parameter = {
  mode: String,
  iconStyle: Object
}

/**
 * 拖拽定位插件
 */
export default {
  name: 'amap-position-picker',
  mixins: [
    ComponentMixins
  ],
  props: Object.assign({

  }, parameter),
  created () {
    this.$componentParameter = parameter
  },
  methods: {
    createComponent () {
      return loadUIPlugins('misc/PositionPicker').then(PositionPicker => {
        this._PositionPicker = PositionPicker
        this.createPlugins()
      })
    },

    getCustomCreateParameter () {
      return {
        map: this.$amap
      }
    },
    /**
     * 创建插件
     */
    createPlugins () {
      this.$amapComponent = new this._PositionPicker(this.$componentCreateParameter)
      if (this.$listeners['success']) {
        this.$amapComponent.on('success', this.$listeners['success'])
      }
      if (this.$listeners['fail']) {
        this.$amapComponent.on('fail', this.$listeners['fail'])
      }
      this.$amapComponent.start()
    }
  }
}
