
import { loadPlugin } from '../../../src/utils/PluginLoader'

import ComponentMixins from '../../mixins/ComponentMixins'

/**
 * 定位插件参数
 * @type {{buttonDom: number, enableHighAccuracy: BooleanConstructor, showCircle: BooleanConstructor, convert: BooleanConstructor, markerOptions: ObjectConstructor, maximumAge: NumberConstructor, timeout: NumberConstructor, buttonPosition: StringConstructor, extensions: StringConstructor, showMarker: BooleanConstructor, useNative: BooleanConstructor, noGeoLocation: BooleanConstructor, circleOptions: ObjectConstructor, showButton: BooleanConstructor, GeoLocationFirst: BooleanConstructor, buttonOffset: ObjectConstructor, panToLocation: BooleanConstructor, zoomToAccuracy: BooleanConstructor, noIpLocate: NumberConstructor}}
 */
const parameter = {
  enableHighAccuracy: {
    type: Boolean,
    default: true
  },
  timeout: Number,
  noIpLocate: Number,
  noGeoLocation: Boolean,
  GeoLocationFirst: Boolean,
  maximumAge: Number,
  convert: {
    type: Boolean,
    default: true
  },
  showButton: {
    type: Boolean,
    default: true
  },
  buttonDom: String | Object,
  buttonPosition: String,
  buttonOffset: Object,
  showMarker: {
    type: Boolean,
    default: true
  },
  markerOptions: Object,
  showCircle: {
    type: Boolean,
    default: true
  },
  circleOptions: Object,
  panToLocation: {
    type: Boolean,
    default: true
  },
  zoomToAccuracy: {
    type: Boolean,
    default: true
  },
  useNative: Boolean,
  extensions: String
}

/**
 * 地图定位组件
 */
export default {
  name: 'amap-geolocation',
  mixins: [
    ComponentMixins
  ],
  props: Object.assign({

  }, parameter),
  created () {
    this.$componentParameter = parameter
  },
  methods: {
    /**
     * 创建组件
     */
    createComponent () {
      return loadPlugin(this.$amap, 'Geolocation').then(() => {
        this.createPlugins()
      })
    },
    /**
     * 创建插件
     */
    createPlugins () {
      this.$amapComponent = new window['AMap'].Geolocation(this.$componentCreateParameter)
      this.$amap.addControl(this.$amapComponent)
      this.$amapComponent.getCurrentPosition()
      if (this.$listeners['complete']) {
        window['AMap'].event.addListener(this.$amapComponent, 'complete', this.$listeners['complete'])
      }
      if (this.$listeners['error']) {
        window['AMap'].event.addListener(this.$amapComponent, 'error', this.$listeners['error'])
      }
    }
  }
}
