<template>
  <div :class="computedContainerClass" style="height: 100%" :id="elementId">
    <slot/>
  </div>
</template>

<script>

import { amapSDKLoader } from '../../../src/utils/AmapSDKLoader'
import EventConstant from '../../../src/constant/EventConstant'

// 高德地图事件
const amapEvent = [
  'complete',
  'click',
  'dblclick',
  'mapmove',
  'hotspotclick',
  'hotspotover',
  'hotspotout',
  'movestart',
  'moveend',
  'zoomchange',
  'zoomstart',
  'zoomend',
  'mousemove',
  'mousewheel',
  'mouseover',
  'mouseout',
  'mouseup',
  'mousedown',
  'rightclick',
  'dragstart',
  'dragging',
  'dragend',
  'resize',
  'touchstart',
  'touchmove',
  'touchend'
]

/**
 * 监控属性执行器
 */
const watchParameterHandlers = {
  zoom: {
    handler: (map, value) => {
      map.setZoom(value)
    },
    changeEvent: ['zoomchange'],
    changeHandler: (map) => {
      return map.getZoom()
    }
  },
  center: {
    handler: (map, value) => {
      map.setCenter(value)
    },
    changeEvent: ['dragend', 'zoomchange'],
    changeHandler: (map) => {
      const center = map.getCenter()
      return [center.lng, center.lat]
    }
  }
}

/**
 * 高德地图参数
 */
const amapParameter = {
  center: Array,
  zoom: Number
}


/**
 * 地图组件
  */
export default {
  name: 'amap-map',
  props: Object.assign({
    // 元素ID
    elementId: {
      type: String,
      default: 'element_' + new Date().getTime()
    },
    // 是否显示高德logo
    showLogo: {
      type: Boolean,
      default: true
    },
    mapStyle: {
      type: String,
      default: 'normal'
    }
  }, amapParameter),
  data () {
    return {
      validatedAmapParameter: [],
      // 标识参数改变是否是地图自身触发
      amapParameterChange: {}
    }
  },
  computed: {
    computedContainerClass () {
      if (!this.showLogo) {
        return 'no-logo-container'
      }
      return ''
    }
  },
  beforeCreate () {
    if (amapSDKLoader === null) {
      throw Error('组件创建失败，未初始化sdk配置')
    } else {
      // 加载sdk
      this.sdkLoadPromise = amapSDKLoader.loadSDK()
    }
  },
  created() {
    // this.validateAmapParameter()
    // 监控参数
    this.watchedParameter()
  },
  mounted () {
    this.createMap()
  },
  methods: {
    /**
     * 监控amap参数
     */
    watchedParameter () {
      const _vue = this
      Object.keys(watchParameterHandlers).forEach(key => {
        _vue.$watch(key, (_new) => {
          if (this.$amap && _vue.amapParameterChange[key] !== true) {
            watchParameterHandlers[key].handler(this.$amap, _new)
          }
          _vue.amapParameterChange[key] = false
        })
      })
    },
    /**
     * 创建高德地图参数
     */
    createAmapParameter () {
      const parameter = {
        mapStyle: this.getMapStyle()
      }
      Object.keys(amapParameter).forEach(key => {
        if (this[key]) {
          parameter[key] = this[key]
        }
      })
      return parameter
    },
    /**
     * 创建地图
     */
    createMap () {
      this.sdkLoadPromise.then(() => {
        this.$amap = new window['AMap'].Map(this.elementId, this.createAmapParameter())
        // 添加事件
        this.addAmapEvent()
        // 为所有子组件添加事件
        this.$children.forEach(childComponent => {
          childComponent.$emit(EventConstant.AMAP_CREATE_EVENT, this.$amap)
        })
      }).catch(error => {
        throw error
      })
    },
    /**
     * 添加地图事件
     */
    addAmapEvent () {
      // 存放事件名称与事件执行器
      const parameterEvent = {}
      // 添加值改变事件
      Object.keys(watchParameterHandlers).forEach(key => {
        // 获取事件
        const changeEventList = watchParameterHandlers[key].changeEvent
        const handler = watchParameterHandlers[key].changeHandler
        if (changeEventList && handler) {
          changeEventList.forEach(event => {
             if (!parameterEvent[event]) {
               parameterEvent[event] = []
             }
            parameterEvent[event].push({
              parameter: key,
              handler: handler
            })
          })
        }
      })
      Object.keys(parameterEvent).forEach(event => {
        const handlers = parameterEvent[event]
        this.$amap.on(event, (e) => {
          // 执行事件
          handlers.forEach(parameterHandler => {
            this.amapParameterChange[parameterHandler.parameter] = true
            this.$emit(`update:${parameterHandler.parameter}`, parameterHandler.handler(this.$amap, e))
          })
          // 触发事件
          if (this.$listeners[event]) {
            this.$emit(event, this.$amap, e)
          }
        })
      })

      // 添加监控事件
      amapEvent.forEach(event => {
        if (this.$listeners[event] && !parameterEvent[event]) {
          this.$amap.on(event, (e) => {
            this.$emit(event, this.$amap, e)
          })
        }
      })
    },
    /**
     * 获取地图样式
     */
    getMapStyle () {
      return `amap://styles/${this.mapStyle}`
    }
  }
}


</script>

<style>
  .no-logo-container .amap-logo {
    display: none !important;
  }
  .no-logo-container .amap-copyright {
    display: none !important;
  }
</style>
