
const defaultConfig = {
  version: '1.4.15',
  key: null,
  UIVersion: null
}

/**
 * SDK加载工具类
 */
export class AmapSDKLoader {
  /**
   * 构造函数
   * @param config
   */
  constructor (config) {
    this.config = {
      ...defaultConfig,
      ...config
    }
  }

  /**
   * 加载SDK
   */
  loadSDK () {
    // 判断是否需要加载
    if (window.AMap && window.AMap.Map && window.AMap.User.key === this.config.key) {
      return this.loadUISDK()
    }
    if (this.sdkPromise) return this.sdkPromise
    const sdkPromise = new Promise((resolve, reject) => {
      const src = `https://webapi.amap.com/maps?v=${this.config.version}&key=${this.config.key}`
      const jsapi = document.createElement('script')
      jsapi.async = true
      jsapi.src = src
      document.head.appendChild(jsapi)
      jsapi.onload = () => {
        const UIPromise = this.config.UIVersion ? this.loadUISDK() : null
        if (UIPromise) {
          UIPromise.then(() => {
            resolve()
          })
        }
      }
      jsapi.onerror = (error) => reject(error)
    })
    this.sdkPromise = sdkPromise
    return sdkPromise
  }

  /**
   * 加载UI SDK
   */
  loadUISDK () {
    if (!this.config.UIVersion || window.AMapUI) return Promise.resolve()
    if (this.uiPromise) return this.uiPromise
    const uiPromise = new Promise((resolve, reject) => {
      const UISDK = document.createElement('script')
      //  TODO 版本号待完善
      UISDK.src = `https://webapi.amap.com/ui/1.0/main.js?v=${this.config.UIVersion}`
      UISDK.async = true
      document.head.appendChild(UISDK)
      UISDK.onload = () => resolve()
      UISDK.onerror = (error) => reject(error)
    })
    this.uiPromise = uiPromise
    return uiPromise
  }
}

let amapSDKLoader = null

/**
 * 初始化skd方法
 * @param config
 */
export const initAMapSDKLoader = (config) => {
  if (!amapSDKLoader) {
    amapSDKLoader = new AmapSDKLoader(config)
    amapSDKLoader.loadSDK()
  }
}

export {
  amapSDKLoader
}
